
.drag-outer .open > .drag_caret > .fa-icon {
  transform: rotate(90deg) !important;
}
.drag-outer .drag_caret,
.drag-outer ._check {
  margin: 0 !important;
  -ms-flex: 0 0 0.32rem;
      flex: 0 0 0.32rem;
  text-align: center;
  cursor: pointer;
}
.drag-outer .drag_caret > .fa-icon {
  transition: transform 0.1s ease;
  fill: #424242;
}
.drag-outer .drag_item {
  cursor: move;
}
.drag-outer .drag_title {
  pointer-events: none;
  transition: transform 0.2s ease;
}
.drag-outer div[end] .drag_caret svg {
  visibility: hidden;
}
.drag-outer #drag-outer {
  overflow: hidden;
}
.drag-outer li {
  position: relative;
}
.drag-outer li[_start] {
  opacity: 0.5;
  background-color: #f5f5f5;
}
.drag-outer li:after {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  height: 0.4rem;
  background-color: #c5e1a5;
  visibility: hidden;
  pointer-events: none;
  transition: transform 0.2s ease;
  transform-origin: 50% 50%;
}
.drag-outer li[_drag="move-before"] > .drag_title {
  transform: translateY(20%);
  color: #8bc34a;
  font-weight: 800;
}
.drag-outer li[_drag="move-after"] > .drag_title {
  transform: translateY(-20%);
  color: #8bc34a;
  font-weight: 800;
}
.drag-outer li[_drag="move-before"]:after {
  visibility: visible;
  transform: translateY(-35%) scale(1, 0.3);
}
.drag-outer li[_drag="move-in"]:after {
  visibility: visible;
  transform: translateX(0.24rem) scale(1, 1);
  opacity: 0.5;
  border-radius: 0.2rem 0 0 0.2rem;
}
.drag-outer li[_drag="move-after"]:after {
  visibility: visible;
  transform: translateY(35%) scale(1, 0.3);
}



