content{
  display: block;
  overflow-y: auto;
  overflow-x: hidden;
  .pc({
    &::-webkit-scrollbar {
      width: 10px;
    }
    &::-webkit-scrollbar-button {
      height: 8px;
      visibility: hidden;
    }
    &::-webkit-scrollbar-track-piece {
      box-shadow: 0 0 2px #bdbdbd inset;
      border-radius: 5px;
      background: #cfcfcf;
    }
    &::-webkit-scrollbar-thumb {
      &:hover {
        background: #8c8c8c;
      }
      background: #9e9e9e;
      border-radius: 5px;
      border: 1px solid #cfcfcf;
    }
  })
}


