
.data-router-outer{
  padding-top: var(--pd-l);
}
#list-zero {
  border-top: var(--ht-s) solid transparent;
  border-bottom: var(--pd-s) solid transparent;
  height: var(--pd-m);
  position: relative;
  background-image: url(./ruler.svg);
  background-size: 49px var(--pd-s);
  background-repeat: repeat-x;
  height: 4px;
  margin-left: var(--pd-m);
  margin-right: var(--pd-m);
  box-sizing: content-box;
  line-height: normal;
}
#list-zero:after,
#list-zero:before {
  content: "";
  position: absolute;
  width: 4px;
  height: 4px;
  left: 0;
  bottom: 0;
  margin-bottom: -0.03rem;
  border: 1px solid #bdbdbd;
  border-radius: 50%;
}
#list-zero:after{
  left: 100%;
  margin-left: -4px;
}
.stage {
  position: absolute;
  left: 0;
  min-width: var(--wd-s);
  text-align: center;
  font-size: var(--fs-xs);
  color: var(--cl-front-i);
  top: 0;
  margin-top: -0.36rem;
  transform: translateX(-50%);
  border: none;
}
.stage div {
  border-radius: var(--raduis-s);
  background: var(--cl-hint);
  transform: translate(0);
  color: var(--cl-front);
  min-width: var(--wd-s);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.stage:before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  left: 0;
  bottom: 0;
  margin-bottom: -0.04rem;
  border-width: 0.16rem 0.16rem 0;
  border-style: solid;
  border-color: var(--cl-hint) transparent transparent;
}
.stage .stage-date {
  background: transparent;
  color: var(--cl-hint);
}
.indicator {
  position: absolute;
  height: var(--ht-m);
  width: 2px;
  background: var(--cl-theme);
  bottom: 0;
  margin-left: -1px;
}
.indicator:after {
  content: attr(start);
  background: var(--cl-theme);
  position: absolute;
  left: 0;
  top: 0;
  height: var(--pd-m);
  line-height: var(--pd-m);
  width: var(--wd-m);
  font-size: var(--fs-xs);
  text-align: center;
  overflow: hidden;
  border-radius: var(--raduis-s);
  margin-left: -0.2rem;
  margin-top: -0.16rem;
  border: 1px solid #9e9e9e;
  color: white;
}



